import Vue from 'vue'
import Router from 'vue-router'


Vue.use(Router)


const router = new Router({
	routes: [
		{
			path: '',
			redirect: 'home'
		},
		{
			path: '/home/:id?',
			name: 'home',
			component: () => import('./views/Home.vue')
		},
		{
			path: '/rule',
			name: 'rule',
			component: () => import('./views/Rule.vue')
		}
	]
})


router.afterEach((to, from, next) => {

	window.scrollTo(0, 0);

});

export default router;
