'use strict'
import { Toast, Dialog  } from 'vant';

const isdebug = true;

export default{
    
    showToast(message){
        Toast({
            type:'text',
            message:message,
            duration: 1500
        });
    },
    showLoading(message){
        var message = message || "加载中...";
        Toast.loading({
            mask: true,
            message: message,
            duration: 30000,
            forbidClick:true
        });
    },
    dismissLoading(){
        Toast.clear();
    },
    showAlert(message,title){
        var message = message;
        var title = title || "系統提示";
        Dialog.alert({
            title: title,
            message: message
        }).then(() => {
            // on close
        });
    },






    //验证手机号码
    regPhone(phone){
        let reg = /^1[34578]\d{9}$/;
        return reg.test(phone);
    },
   


    // info message
    info(...text) {
        this.log("[info]", `#909399`, `#FFFFFF`, ...text);
    },

    // success message
    success(...text) {
        this.log("[success]", `#67C23A`, `#FFFFFF`, ...text);
    },

    // warning message
    warn(...text) {
        this.log("[warning]", `#E6A23C`, `#FFFFFF`, ...text);
    },

    // error message
    err(...text) {
        this.log("[error]", `#F56C6C`, `#FFFFFF`, ...text);
    },

    /**
     * console log
     * @param text 
     * @param bgcolor 
     * @param color 
     * @param details oth message
     */
    log(text, bgcolor, color, ...details) {
        isdebug &&
            window.console.log(
                `%c${text}`,
                `background-color: ${bgcolor}; color:${color}; padding: 2px 5px; border-radius: 2px`,
                ...details);
    },


}